	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Donkey On The Edge","date":"30 Jan 2002","slug":"/2002-01-30-donkey-on-the-edge/","tags":"Y2002"},"html":"<p>From the strange-taste-can-be-inherited department: Christopher just insisted on eating his cottage cheese with a dab of salsa. Just like his dad. Film at eleven.</p>\n<p>This one is actually more  for <a href=\"http://www.curry.com\">Adam Curry</a> than for me, but German TV Network ZDF has gotten lots of protests against <a href=\"http://www.spiegel.de/panorama/0,1518,179523,00.html\">the dress (or lack thereof</a>... in German, but pictures tell all) worn by singer Sarah Connor in last Saturday's Wetten Dass show. </p>\n<p>New title line, see if it shows up on weblogs.com.</p>\n<p>Update 2005: The Spiegel page is now pay-per-view, but pictures of Sarah Connor's wardrobe malfunction are also <a href=\"http://www.rp-online.de/public/bildershow_main/nachrichten/journal/gesellschaft/6287?picture_id=0\">here</a>.</p>"},"next":{"fields":{"title":"Stop It, Please Don't Stop","date":"29 Jan 2002","slug":"/2002-01-29-stop-it-please-dont-stop/"}},"previous":{"fields":{"title":"Stem Cells","date":"30 Jan 2002","slug":"/2002-01-30-stem-cells/"}}},{"node":{"fields":{"title":"Stop It, Please Don't Stop","date":"29 Jan 2002","slug":"/2002-01-29-stop-it-please-dont-stop/","tags":"Y2002"},"html":"<p>IWon: <a href=\"http://money.iwon.com/jsp/nw/nwdt_rt.jsp?cat=USMARKET&src=201&feed=reu&section=news&news_id=reu-l29305613&date=20020129&alias=/alias/money/cm/nw\">German opposition prepared to drop A400M court bid</a>. I don't really get this. Sure the government is trying to buy more planes than they budgeted. The opposition isn't opposed to the additional planes, they just want to embarass the government by reopening the budget. But involving the Constitutional Court?<br />\nIn the US, the government gets shut down all the time due to budget squabbles, and the Supreme Court never steps in.<br />\n<a href=\"http://c.moreover.com/click/here.pl?x31207647\">Update</a> The government has delayed the legal commitment for the additional planes until next year, and the oppostion has withdrawn their suit. In other words, both sides have declared victory and gone home.</p>"},"next":{"fields":{"title":"Es geht mir gut","date":"29 Jan 2002","slug":"/2002-01-29-es-geht-mir-gut/"}},"previous":{"fields":{"title":"Donkey On The Edge","date":"30 Jan 2002","slug":"/2002-01-30-donkey-on-the-edge/"}}},{"node":{"fields":{"title":"Es geht mir gut","date":"29 Jan 2002","slug":"/2002-01-29-es-geht-mir-gut/","tags":"Y2002"},"html":"<p><a href=\"http://dailynews.yahoo.com/h/ap/20020129/wl/europe_storms_1.html\">Storms in Europe kill at least 16</a>, including 6 in northern Germany (according to ARD) with winds up to 150 kmh. A wind generator was blown to the ground up near Husum. The wind has been quite loud here the past few nights. I thought our satellite dish would blow away for sure.   </p>\n<p>Bad day yesterday. We had been out vistiting on Sunday, Christopher skipped his nap and at 6:30pm fell asleep in the car on the way home. We let him sleep, thinking he'd be up a bit earlier than usual in the morning. A 'bit' was good... at 4:30am he was already up and at 'em. I didn't recover from that until well after noon.</p>\n<p>You didn't hear it from me, but if you look at your nearest <a href=\"http://www.freebsd.org\">FreeBSD</a> mirror, you might find the <a href=\"ftp://ftp.freebsd.org/pub/FreeBSD/releases/i386/4.5-RELEASE/\">upcoming 4.5 release</a>.</p>"},"next":{"fields":{"title":"Testing and Tweaking","date":"28 Jan 2002","slug":"/2002-01-28-testing-and-tweaking/"}},"previous":{"fields":{"title":"Stop It, Please Don't Stop","date":"29 Jan 2002","slug":"/2002-01-29-stop-it-please-dont-stop/"}}}],"pathPrefix":"","first":false,"last":false,"index":935,"pageCount":1101}}

